footer{
	margin-bottom: 6%;
}
.status_container,.social{
	width: 48.5%;
	margin-bottom: 5%;
}
.status_container>p.light{
	width: 30%;
}
.social>li{
	list-style: none;
	padding-right: 15px;
	float: left;
		.fa{
			font-size: 1rem;
			color: #bbb;
			transition: .2s all ease;
